import * as React from 'react';
import { MenuItem } from '@ali/we-design';
;
import { MenuItemInnerProps } from '@ali/4ever-component';
import { insertOnlineVideo } from '../actions';

export default function OnlineVideoMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;
  const isDisabled = controller.query('isSelectionInTable', { excludeColumns: true })
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInLinkCard');

  const handleClick = () => {
    if (isDisabled) {
      return;
    }
    controller.run('onAction', insertOnlineVideo());
  };

  return (
    <MenuItem {...rest} onClick={handleClick} disabled={isDisabled}>
      {title || ''}
    </MenuItem>
  );
}
